import { type JSX } from 'react';
import { Highlight as HighlightType } from '@custom-types/highlight';
import { HighlightList } from '@components/sidebar/highlights/HighlightList';
import { Group, Text } from '@mantine/core';
import { useTabInfo } from '@context/TabInfoContext';
import { NoHighlights } from '@components/sidebar/highlights/NoHighlights';
import './HighlightsUrl.css';

type HighlightsUrlProps = {
  highlights: HighlightType[];
  onDelete: (highlight: HighlightType) => void;
  searchQuery: string;
};

/**
 * The highlights for the current URL.
 */
export function HighlightsUrl({
  highlights,
  onDelete,
  searchQuery,
}: HighlightsUrlProps): JSX.Element {
  const { tabInfo } = useTabInfo();
  let pageContext: JSX.Element | null = null;
  if (tabInfo.title || tabInfo.favIconUrl) {
    pageContext = (
      <>
        <Group justify="space-between" wrap="nowrap" m={8} mb={16}>
          {tabInfo.title ? (
            <Text fw="bold" className="word-break-all">
              {tabInfo.title}
            </Text>
          ) : null}
          {tabInfo.favIconUrl ? (
            // We don't want to show an alt if no favicon is available.
            <img src={tabInfo.favIconUrl} width={24} height={24} />
          ) : null}
        </Group>
      </>
    );
  }

  return (
    <>
      {pageContext}
      {highlights.length > 0 ? (
        <HighlightList highlights={highlights} onDelete={onDelete} />
      ) : (
        <NoHighlights searchQuery={searchQuery} />
      )}
    </>
  );
}
