import { JSX, ReactElement } from 'react';
import { Box, Divider, Stack, Title } from '@mantine/core';
import { SwitchOption } from '@components/sidebar/settings/options/SwitchOption';

type SettingsSectionProps = {
  title: string;
  children:
    | ReactElement<typeof SwitchOption>
    | ReactElement<typeof SwitchOption>[];
  withDivider?: boolean;
};

/**
 * One section for one category of settings.
 */
export function SettingsSection({
  title,
  children,
  withDivider = false,
}: SettingsSectionProps): JSX.Element {
  if (!Array.isArray(children)) {
    children = [children];
  }

  return (
    <Box>
      <Title order={3} size="h4" mb={8}>
        {title}
      </Title>
      <Stack gap="xs">{children}</Stack>
      {withDivider ? <Divider mt="md" /> : null}
    </Box>
  );
}
