import { Highlight } from '@custom-types/highlight';
import {
  filterHighlights,
  groupHighlightsByUrl,
  sortHighlightsByDate,
} from '@utils/highlight';
import { UserSettings } from '@custom-types/userSettings';
import { HighlightDatabase } from '@store/highlights';
import {
  extendMarkdownOfHighlightsWithSource,
  groupedHighlightsMarkdownToMarkdown,
  groupedHighlightsTextToMarkdown,
  highlightsTextToMarkdown,
} from '@utils/markdown';

/**
 * Export data by downloading the data in a file.
 */
export function exportString(data: string, fileName: string): void {
  const blob = new Blob([data], { type: 'text/markdown' });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = fileName;
  link.click();
  URL.revokeObjectURL(url);
}

/** Get the file path for the export. */
export function getFilePath(title?: string): string {
  title = title?.replaceAll(' ', '_');
  return `highlights${title ? '-' + title : ''}-${Date.now()}.md`;
}

/**
 * Get a markdown string for highlights.
 * The highlights are retrieved and preprocessed according to the user settings.
 */
export async function convertHighlightsToMarkdown(
  userSettings: UserSettings,
  searchQuery: string,
  db: HighlightDatabase,
  markExported: (highlights: Highlight[]) => void,
  url: string,
  title?: string
): Promise<string> {
  if (userSettings.exportForUrl) {
    return convertHighlightsForUrlToMarkdown(
      userSettings,
      searchQuery,
      db,
      markExported,
      url,
      title
    );
  }

  return convertAllHighlightsToMarkdown(
    userSettings,
    searchQuery,
    db,
    markExported
  );
}

/**
 * Get the highlights for the given URL as markdown string.
 * The highlights are preprocessed according to the user settings.
 */
async function convertHighlightsForUrlToMarkdown(
  userSettings: UserSettings,
  searchQuery: string,
  db: HighlightDatabase,
  markExported: (highlights: Highlight[]) => void,
  url: string,
  title?: string
): Promise<string> {
  let highlights: Highlight[];
  if (userSettings.exportNewHighlightsOnly) {
    highlights = await db.getNonExportedHighlightsForUrl(url);
  } else {
    highlights = await db.getHighlightsForUrl(url);
  }

  if (userSettings.exportFilteredOnly) {
    highlights = filterHighlights(highlights, searchQuery);
  }

  highlights = sortHighlightsByDate(highlights);
  markExported(highlights);

  if (userSettings.exportHighlightsFromText) {
    return highlightsTextToMarkdown(highlights, url, title);
  }

  return extendMarkdownOfHighlightsWithSource(highlights, url, title);
}

/**
 * Get the highlights for all URLs in as a markdown string.
 * The highlights are preprocessed according to the user settings.
 */
async function convertAllHighlightsToMarkdown(
  userSettings: UserSettings,
  searchQuery: string,
  db: HighlightDatabase,
  markExported: (highlights: Highlight[]) => void
): Promise<string> {
  let highlights: Highlight[];
  if (userSettings.exportNewHighlightsOnly) {
    highlights = await db.getNonExportedHighlights();
  } else {
    highlights = await db.getHighlights();
  }

  if (userSettings.exportFilteredOnly) {
    highlights = filterHighlights(highlights, searchQuery);
  }

  markExported(highlights);

  const highlightsByUrl = groupHighlightsByUrl(
    sortHighlightsByDate(highlights)
  );

  if (userSettings.exportHighlightsFromText) {
    return groupedHighlightsTextToMarkdown(highlightsByUrl);
  }

  return groupedHighlightsMarkdownToMarkdown(highlightsByUrl);
}
