import Browser from 'webextension-polyfill';
import {
  DEFAULT_USER_SETTINGS,
  UserSettings,
} from '@custom-types/userSettings';

/** Store user settings in local storage. */
export async function storeUserSettings(
  userSettings: UserSettings
): Promise<void> {
  await Browser.storage.local.set({ userSettings: userSettings });
}

/** Retrieve user settings from local storage. */
export async function getUserSettings(): Promise<UserSettings> {
  const userSettingsStore: { userSettings?: UserSettings } =
    await Browser.storage.local.get('userSettings');
  if (userSettingsStore.userSettings === undefined) {
    return DEFAULT_USER_SETTINGS;
  }

  // We merge the user settings with the default settings to ensure that all settings are present.
  // This is necessary when introducing new settings that might not yet be stored in the user's browser.
  return { ...DEFAULT_USER_SETTINGS, ...userSettingsStore.userSettings };
}
