import { Highlight as HighlightType } from '@custom-types/highlight';
import { JSX } from 'react';
import { Highlight } from '@components/sidebar/highlights/highlight/Highlight';
import { sortHighlightsByDate } from '@utils/highlight';

type HighlightsListProps = {
  highlights: HighlightType[];
  onDelete: (highlight: HighlightType) => void;
  allHighlights?: boolean;
};

/**
 * Display highlights in a list.
 */
export function HighlightList({
  highlights,
  onDelete,
  allHighlights = false,
}: HighlightsListProps): JSX.Element {
  const sortedHighlights = sortHighlightsByDate(highlights);

  return (
    <ul className="highlight-list">
      {sortedHighlights.map((highlight) => (
        <li key={highlight.id}>
          <Highlight
            highlight={highlight}
            onDelete={onDelete}
            allHighlights={allHighlights}
          />
        </li>
      ))}
    </ul>
  );
}
