import { ReactElement } from 'react';
import { Modal, Stack } from '@mantine/core';
import { useUserSettings } from '@context/UserSettingsContext';
import { ExportSettings } from '@components/sidebar/settings/export/ExportSettings';
import { ExportActions } from '@components/sidebar/export/ExportActions';

type ExportModalProps = {
  searchQuery: string;
  opened: boolean;
  close: () => void;
};

/**
 * A full-screen modal to display export actions and settings.
 */
export function ExportModal({
  searchQuery,
  opened,
  close,
}: ExportModalProps): ReactElement {
  const { userSettings, setUserSettings } = useUserSettings();

  return (
    <Modal
      opened={opened}
      onClose={close}
      title="Export highlights"
      fullScreen
      radius="0"
      zIndex="2000"
    >
      <Stack gap="lg">
        <ExportActions searchQuery={searchQuery} userSettings={userSettings} />
        <ExportSettings
          userSettings={userSettings}
          setUserSettings={setUserSettings}
          searchQuery={searchQuery}
        />
      </Stack>
    </Modal>
  );
}
