import { forwardRef, JSX } from 'react';
import { ActionIcon } from '@mantine/core';

type ActionProps = {
  onClick?: () => void;
  icon: JSX.Element;
  label: string;
};

/**
 * A generic action with an icon.
 */
export const Action = forwardRef<HTMLButtonElement, ActionProps>(
  function Action({ onClick, label, icon }: ActionProps, ref): JSX.Element {
    return (
      <ActionIcon
        size="lg"
        variant="subtle"
        color="dark.5"
        onClick={onClick}
        aria-label={label}
        ml={8}
        ref={ref}
      >
        {icon}
      </ActionIcon>
    );
  }
);
