import { Color } from '@highlighter/util/color';

/** Settings for the plugin defined by the user.  */
export type UserSettings = {
  autoHighlighting: boolean;
  extendedHighlighting: boolean;
  displayHighlightsAsText: boolean;

  exportFilteredOnly: boolean;
  exportHighlightsFromText: boolean;
  exportForUrl: boolean;
  exportNewHighlightsOnly: boolean;

  obsidianSync: boolean;
  obsidianSyncUrl: string;

  highlightColorSelection: Color;
  highlightingDisabled: boolean;
};

/** The default settings. */
export const DEFAULT_USER_SETTINGS: UserSettings = {
  autoHighlighting: false,
  extendedHighlighting: true,
  displayHighlightsAsText: false,

  exportFilteredOnly: true,
  exportHighlightsFromText: false,
  exportForUrl: true,
  exportNewHighlightsOnly: false,

  obsidianSync: false,
  obsidianSyncUrl: 'http://localhost:9090',

  highlightColorSelection: Color.Green,
  highlightingDisabled: false,
};
