import { ReactNode } from 'react';
import { Alert, Modal, Skeleton, Stack } from '@mantine/core';
import { ErrorLog } from '@components/sidebar/error-log/ErrorLog';
import { ErrorLogEntry } from '@custom-types/errorLog';
import { useDeleteErrorLogEntry } from '@store/errorLogs';

type ErrorLogModalProps = {
  errorLogEntries: ErrorLogEntry[];
  opened: boolean;
  close: () => void;
  isPending: boolean;
  error: Error | null;
};

/**
 * A full-screen modal to display log entries for client-side application
 * errors.
 */
export function ErrorLogModal({
  errorLogEntries,
  opened,
  close,
  isPending,
  error,
}: ErrorLogModalProps): ReactNode {
  const { mutate: deleteLogEntry } = useDeleteErrorLogEntry();

  let modalContent: ReactNode;
  if (isPending) {
    modalContent = (
      <Stack gap="xs">
        <Skeleton height={40} />
        <Skeleton height={40} />
        <Skeleton height={40} />
      </Stack>
    );
  } else if (error != null) {
    modalContent = (
      <Alert variant="light" color="red" title="Error loading error log">
        The error log could not be loaded: {error.message}
      </Alert>
    );
  } else {
    modalContent = (
      <ErrorLog
        errorLogEntries={errorLogEntries}
        deleteLogEntry={deleteLogEntry}
      />
    );
  }

  return (
    <Modal
      opened={opened}
      onClose={close}
      title="Error Log"
      fullScreen
      radius="0"
      zIndex="2000"
    >
      {modalContent}
    </Modal>
  );
}
