import { JSX, ReactNode, useState } from 'react';
import { Highlights } from '@components/sidebar/highlights/Highlights';
import { TopBar } from '@components/sidebar/top-bar/TopBar';
import { Route } from '@custom-types/routing';
import { Settings } from '@components/sidebar/settings/Settings';
import { useMessageHandler } from '@hooks/useMessageHandler';
import { useDebouncedValue } from '@mantine/hooks';
import { Box } from '@mantine/core';
import './Sidebar.css';
import { useTabEvents } from '@hooks/useTabEvents';
import { BottomBar } from '@components/sidebar/bottom-bar/BottomBar';

/**
 * Entry point for the sidebar.
 */
export function Sidebar(): JSX.Element {
  const [route, setRoute] = useState<Route>(Route.Highlights);
  useMessageHandler();
  useTabEvents();

  const [searchQuery, setSearchQuery] = useState<string>('');
  const [debouncedSearchQuery] = useDebouncedValue(searchQuery, 200);

  let content: ReactNode = null;
  if (route === Route.Highlights) {
    content = <Highlights searchQuery={debouncedSearchQuery} />;
  }

  if (route === Route.Settings) {
    content = <Settings searchQuery={searchQuery} />;
  }

  return (
    <Box h="100vh">
      <Box className="sidebar-wrapper">
        <TopBar
          route={route}
          setRoute={setRoute}
          searchQuery={searchQuery}
          setSearchQuery={setSearchQuery}
        />
      </Box>
      <Box className="content">{content}</Box>
      <Box>
        <BottomBar />
      </Box>
    </Box>
  );
}
