import { ReactElement } from 'react';
import { Tooltip } from '@mantine/core';
import { IconCheck, IconStarFilled } from '@tabler/icons-react';

type ExportIndicatorProps = {
  exported: boolean;
};

const ICON_SIZE = 12;

/** An indicator to denote whether a highlight has already been exported. */
export function ExportIndicator({
  exported,
}: ExportIndicatorProps): ReactElement {
  if (exported) {
    return (
      <Tooltip label="Exported higlight">
        <IconCheck size={ICON_SIZE} color="var(--mantine-color-green-3)" />
      </Tooltip>
    );
  }

  return (
    <Tooltip label="New highlight">
      <IconStarFilled size={ICON_SIZE} />
    </Tooltip>
  );
}
