import { storeErrorLogEntry } from '@store/errorLogs';
import { getActiveTab } from '@utils/tab';
import Browser from 'webextension-polyfill';
import { MessageType } from '@custom-types/message';

/**
 * Write an error to the client-side log and push the error message
 * to the content script.
 */
export async function logAndPushError(title: string, description: string) {
  logError(title, description);
  pushErrorToContentScript(title, description);
}

/**
 * Push an error via message to the content script.
 */
async function pushErrorToContentScript(title: string, description: string) {
  const activeTab = await getActiveTab();
  if (activeTab?.id != null) {
    Browser.tabs.sendMessage(activeTab.id, {
      type: MessageType.Error,
      payload: {
        title,
        description,
      },
    });
  }
}

/**
 * Write an error log to the client-side log.
 * Errors are stored in local storage.
 *
 * IDs are hashes of title and description so that we can check if an error with
 * the same title and description already exists.
 */
async function logError(title: string, description: string) {
  const id = await getHash(title + description);
  const errorLog = {
    id,
    title,
    description,
    createdAt: new Date(),
  };

  return storeErrorLogEntry(errorLog);
}

export async function getHash(message: string): Promise<string> {
  const encoder = new TextEncoder();
  const data = encoder.encode(message);
  const hashBuffer = await crypto.subtle.digest('SHA-1', data);
  const hashArray = Array.from(new Uint8Array(hashBuffer));
  return hashArray.map((b) => b.toString(16).padStart(2, '0')).join('');
}
