import { useCallback, useEffect } from 'react';
import { setTabInfoForActiveTab } from '@utils/tab';
import { useHighlights } from '@context/HighlightsContext';
import { useTabInfo } from '@context/TabInfoContext';
import Browser from 'webextension-polyfill';

/**
 * Register tab event listeners.
 */
export function useTabEvents() {
  const { refetchHighlights, refetchHighlightsFromTab } = useHighlights();
  const { setTabInfo } = useTabInfo();

  const onTabEvent = useCallback(() => {
    refetchHighlights();
    refetchHighlightsFromTab();
    setTabInfoForActiveTab(setTabInfo);
  }, [refetchHighlights, refetchHighlightsFromTab, setTabInfo]);

  useEffect(() => {
    setTabInfoForActiveTab(setTabInfo);
  }, [setTabInfo]);

  useEffect(() => {
    Browser.tabs.onUpdated.addListener(onTabEvent);
    Browser.tabs.onActivated.addListener(onTabEvent);
    return () => {
      Browser.tabs.onUpdated.removeListener(onTabEvent);
      Browser.tabs.onActivated.removeListener(onTabEvent);
    };
  }, [onTabEvent, setTabInfo]);
}
