import { Dispatch, JSX, ReactNode, SetStateAction } from 'react';
import { ActionIcon, Flex, TextInput, Text } from '@mantine/core';
import './TopBar.css';
import { Route } from '@custom-types/routing';
import {
  IconArrowLeft,
  IconSettings,
  IconSearch,
  IconDownload,
} from '@tabler/icons-react';
import { Action } from '@components/sidebar/top-bar/actions/Action';
import { useDisclosure } from '@mantine/hooks';
import { ExportModal } from '@components/sidebar/export/ExportModal';

type TopBarProps = {
  route: Route;
  setRoute: Dispatch<SetStateAction<Route>>;
  searchQuery: string;
  setSearchQuery: Dispatch<SetStateAction<string>>;
};

/**
 * Component for the top bar that hosts the navigation and search.
 */
export function TopBar({
  route,
  setRoute,
  searchQuery,
  setSearchQuery,
}: TopBarProps): JSX.Element {
  const [
    exportModalOpened,
    { open: openExportModal, close: closeExportModal },
  ] = useDisclosure(false);

  let topBarContent: ReactNode = null;
  switch (route) {
    case Route.Highlights:
      topBarContent = (
        <>
          <TextInput
            rightSection={<IconSearch />}
            placeholder="Find highlights"
            flex="1"
            radius="md"
            size="md"
            variant="filled"
            value={searchQuery}
            onChange={(event) => setSearchQuery(event.currentTarget.value)}
          />
          <Action
            label="Open settings"
            onClick={() => setRoute(Route.Settings)}
            icon={<IconSettings />}
          />
          <Action
            icon={<IconDownload />}
            label="Export highlights"
            onClick={openExportModal}
          />
          <ExportModal
            searchQuery={searchQuery}
            opened={exportModalOpened}
            close={closeExportModal}
          />
        </>
      );
      break;
    case Route.Settings:
      topBarContent = (
        <>
          <ActionIcon
            size="lg"
            variant="subtle"
            color="dark.5"
            onClick={() => setRoute(Route.Highlights)}
            aria-label="Return to highlights"
            className="back-button"
          >
            <IconArrowLeft />
          </ActionIcon>
          <Text fz="xl" c="dark.9" w="100%" ta="center">
            Settings
          </Text>
        </>
      );
  }

  return (
    <Flex
      className="top-bar"
      direction="row"
      wrap="nowrap"
      align="center"
      bg="gray.1"
      p={8}
    >
      {topBarContent}
    </Flex>
  );
}
