import Browser from 'webextension-polyfill';
import { Highlight } from '@custom-types/highlight';
import { HighlightDatabase } from '@store/highlights';

/**
 * Fetch highlights for the current URL and all URLs.
 */
export async function fetchHighlights(
  db: HighlightDatabase,
  setHighlights: (highlights: Highlight[]) => void,
  setHighlightsFromTab: (highlights: Highlight[]) => void
): Promise<void> {
  const tabs = await Browser.tabs.query({
    currentWindow: true,
    active: true,
  });

  const url = tabs[0].url;

  const highlightsPromise = db.getHighlights();
  if (url != undefined) {
    const highlightFromCurrentTabPromise = db.getHighlightsForUrl(url);
    setHighlightsFromTab(await highlightFromCurrentTabPromise);
  }
  setHighlights(await highlightsPromise);
}

/**
 * Filter highlights by a given search query.
 */
export function filterHighlights(
  highlights: Highlight[],
  searchQuery: string
): Highlight[] {
  if (searchQuery === '') {
    return highlights;
  }

  return highlights.filter((highlight) => {
    if (highlight.url.toLowerCase().includes(searchQuery.toLowerCase())) {
      return true;
    }

    if (highlight.comment?.toLowerCase().includes(searchQuery.toLowerCase())) {
      return true;
    }

    return highlight.text.toLowerCase().includes(searchQuery.toLowerCase());
  });
}

/**
 * Groups the given highlights by URL.
 * @return A mapping of URL to highlights.
 */
export function groupHighlightsByUrl(
  highlights: Highlight[]
): Map<string, Highlight[]> {
  const highlightsByUrl = new Map<string, Highlight[]>();
  for (const highlight of highlights) {
    const url = highlight.url;
    if (!highlightsByUrl.has(url)) {
      highlightsByUrl.set(url, []);
    }
    highlightsByUrl.get(url)!.push(highlight);
  }

  return highlightsByUrl;
}

/** Return highlights in a new array sorted by creation date. */
export function sortHighlightsByDate(highlights: Highlight[]): Highlight[] {
  return [...highlights].sort(
    (a, b) => new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
  );
}
