import Browser from 'webextension-polyfill';
import { TabInfo } from '@custom-types/tabInfo';

/**
 * Returns the currently active tab or null if no there is no active tab.
 */
export async function getActiveTab(): Promise<Browser.Tabs.Tab | null> {
  const tabs = await Browser.tabs.query({ active: true, currentWindow: true });
  if (tabs[0]?.id != null) {
    return tabs[0];
  }

  return null;
}

/**
 * Set the tab info of the currently active tab.
 */
export async function setTabInfoForActiveTab(
  setTabInfo: (tabInfo: TabInfo) => void
): Promise<void> {
  const tabInfoFromActiveTab = await getTabInfoFromActiveTab();
  if (tabInfoFromActiveTab !== null) {
    setTabInfo(tabInfoFromActiveTab);
  }
}

/**
 * Returns the title and favicon of the currently active tab or null if there is no active tab.
 */
async function getTabInfoFromActiveTab(): Promise<TabInfo | null> {
  const tab = await getActiveTab();
  if (tab === null) {
    return null;
  }

  return {
    title: tab.title,
    favIconUrl: tab.favIconUrl,
    url: tab.url,
  };
}
