import { createContext, JSX, ReactNode, useContext } from 'react';
import { HighlightDatabase } from '@store/highlights';

/**
 * Context to access the IndexedDB database for highlights.
 */
const HighlightsDBContext = createContext<HighlightDatabase | null>(null);

type HighlightsDBProviderProps = {
  children: ReactNode;
  highlightDatabase: HighlightDatabase;
};

/**
 * Provider for the HighlightsDBContext.
 */
export function HighlightsDBProvider({
  children,
  highlightDatabase,
}: HighlightsDBProviderProps): JSX.Element {
  return (
    <HighlightsDBContext.Provider value={highlightDatabase}>
      {children}
    </HighlightsDBContext.Provider>
  );
}

/**
 * Hook to access the IndexedDB database to store and retrieve highlights.
 */
export function useHighlightsDB(): HighlightDatabase {
  const context = useContext(HighlightsDBContext);
  if (context == null) {
    throw new Error(
      'useHighlightsDB must be used within a HighlightsDBProvider'
    );
  }
  return context;
}
