import { Loader, UnstyledButton } from '@mantine/core';
import { ReactElement, useState } from 'react';

import './ExportAction.css';
import { IconCheck } from '@tabler/icons-react';

type ExportActionProps = {
  title: string;
  icon: ReactElement;
  onClick: () => Promise<void>;
};

/**
 * A single action to export highlights.
 */
export function ExportAction({
  title,
  icon,
  onClick,
}: ExportActionProps): ReactElement {
  const [showConfirmation, setShowConfirmation] = useState(false);
  const [pending, setPending] = useState(false);

  const onAction = async () => {
    setPending(true);
    await onClick();
    setPending(false);
    setShowConfirmation(true);
    setTimeout(() => setShowConfirmation(false), 3000);
  };

  return (
    <UnstyledButton
      onClick={onAction}
      variant="subtle"
      className="export-action"
    >
      <span>
        <span className="left">{icon}</span>
        <span className="title">{title}</span>
        <span className="right">
          {pending && !showConfirmation ? <Loader size="sm" /> : null}
          {!pending && showConfirmation ? <IconCheck color="green" /> : null}
        </span>
      </span>
    </UnstyledButton>
  );
}
