import { type JSX } from 'react';
import { Highlight as HighlightType } from '@custom-types/highlight';
import { HighlightList } from '@components/sidebar/highlights/HighlightList';
import { Accordion, Anchor } from '@mantine/core';
import { groupHighlightsByUrl } from '@utils/highlight';
import { NoHighlights } from '@components/sidebar/highlights/NoHighlights';

type HighlightsAllProps = {
  highlights: HighlightType[];
  onDelete: (highlight: HighlightType) => void;
  searchQuery: string;
};

/**
 * All highlights grouped by URL.
 */
export function HighlightsAll({
  highlights,
  onDelete,
  searchQuery,
}: HighlightsAllProps): JSX.Element {
  const highlightsByUrl = groupHighlightsByUrl(highlights);

  return (
    <Accordion multiple>
      {highlightsByUrl.size > 0 ? (
        <>
          {Array.from(highlightsByUrl).map(([url, highlightsForUrl]) => {
            return (
              <Accordion.Item key={url} value={url}>
                <Accordion.Control>
                  <Anchor
                    href={url}
                    underline="hover"
                    onClick={(e) => e.stopPropagation()}
                  >
                    {url}
                  </Anchor>
                </Accordion.Control>
                <Accordion.Panel>
                  <HighlightList
                    highlights={highlightsForUrl}
                    onDelete={onDelete}
                    allHighlights
                  />
                </Accordion.Panel>
              </Accordion.Item>
            );
          })}
        </>
      ) : (
        <NoHighlights searchQuery={searchQuery} />
      )}
    </Accordion>
  );
}
