import { Highlight } from '@custom-types/highlight';
import { UserSettings } from '@custom-types/userSettings';

/**
 * A message sent between the background script and the content script.
 */
export type Message = {
  type: MessageType;
  payload: Highlight | Highlight[] | UserSettings | string | ErrorLogPayload;
};

/**
 * The type of available messages.
 */
export enum MessageType {
  /** Inform the background script to create a new highlight. */
  StoreHighlightInDB = 'store-highlight',
  /** Inform the sidebar that the highlights should be refetched. */
  RefetchHighlights = 'refetch-highlights',
  GetHighlights = 'get-highlights',
  GetHighlight = 'get-highlight',
  UpdateHighlight = 'update-highlight',
  DeleteFromStore = 'delete-highlight-from-store',
  DeleteFromHighlighter = 'delete-highlight-from-highlighter',
  TabUpdate = 'tab-update',
  GetUserSettings = 'get-user-settings',
  /** Info that the user settings have been updated. */
  UserSettingsUpdate = 'user-settings-update',
  /** Request to update the user settings. */
  UpdateUserSettings = 'update-user-settings',
  Error = 'error',
}

/** Payload for an error log entry send to the content script. */
export type ErrorLogPayload = { title: string; description: string };
