import { Highlight as HighlightType } from '@custom-types/highlight';
import { ReactNode, useEffect, useRef, useState } from 'react';
import { useHighlights } from '@context/HighlightsContext';
import { ActionIcon, Textarea, Tooltip } from '@mantine/core';
import { IconDeviceFloppy } from '@tabler/icons-react';

type SidebarCommentEditorProps = {
  highlight: HighlightType;
};

/** Edit comments of a highlight in the sidebar. */
export function SidebarCommentEditor({
  highlight,
}: SidebarCommentEditorProps): ReactNode {
  const { updateHighlight } = useHighlights();

  const [comment, setComment] = useState<string | null>(
    highlight.comment ?? null
  );

  const textAreaRef = useRef<HTMLTextAreaElement>(null);

  useEffect(() => {
    if (comment == '') {
      textAreaRef.current?.focus();
    }
  }, [comment]);

  if (comment == null) {
    return null;
  }

  const saveComment = () => {
    let updatedHighlight: HighlightType;
    if (comment.trim() === '') {
      updatedHighlight = {
        ...highlight,
        comment: undefined,
      };
    } else {
      updatedHighlight = {
        ...highlight,
        comment: comment.trim(),
      };
    }

    setComment(comment);
    updateHighlight(updatedHighlight);
  };

  return (
    <Textarea
      autosize
      onChange={(e) => setComment(e.target.value)}
      value={comment}
      ref={textAreaRef}
      rightSection={
        comment !== highlight.comment ? (
          <Tooltip label="Save">
            <ActionIcon variant="light" onClick={saveComment} aria-label="save">
              <IconDeviceFloppy />
            </ActionIcon>
          </Tooltip>
        ) : null
      }
    />
  );
}
