import { ReactNode } from 'react';
import { Stack } from '@mantine/core';
import { ErrorLogEntry } from '@custom-types/errorLog';
import { LogEntry } from '@components/sidebar/error-log/ErrorLogEntry';

type ErrorLogProps = {
  errorLogEntries: ErrorLogEntry[];
  deleteLogEntry: (entryId: string) => void;
};

/**
 * An error log that contains error log entries.
 */
export function ErrorLog({
  errorLogEntries,
  deleteLogEntry,
}: ErrorLogProps): ReactNode {
  const sortedErrorLogEntries = [...errorLogEntries].sort(
    (a, b) => b.createdAt.getTime() - a.createdAt.getTime()
  );

  return (
    <Stack gap="xs">
      {sortedErrorLogEntries.map((logEntry) => (
        <LogEntry
          key={logEntry.id}
          errorLogEntry={logEntry}
          deleteEntry={() => deleteLogEntry(logEntry.id)}
        />
      ))}
    </Stack>
  );
}
