import { ReactNode, useEffect } from 'react';
import { ActionIcon, Group, Tooltip } from '@mantine/core';
import { IconExclamationCircle } from '@tabler/icons-react';
import { useDisclosure } from '@mantine/hooks';
import { ErrorLogModal } from '@components/sidebar/error-log/ErrorLogModal';
import { useErrorLog } from '@store/errorLogs';

import './BottomBar.css';
import { useUserSettings } from '@context/UserSettingsContext';
import { DisableHighlightingAction } from '@components/sidebar/bottom-bar/DisableHighlightingAction';

/**
 * Component for the bottom bar that hosts quick actions and error notifications.
 */
export function BottomBar(): ReactNode {
  const [errorModalOpened, { open: openErrorModal, close: closeErrorModal }] =
    useDisclosure(false);
  const { isPending, error, data: errorLogEntries } = useErrorLog(5000);
  const { userSettings, setUserSettings } = useUserSettings();

  useEffect(() => {
    if (errorLogEntries?.length === 0) {
      closeErrorModal();
    }
  }, [closeErrorModal, errorLogEntries?.length]);

  return (
    <>
      <Group bg="gray.1" className="bottom-bar" justify="space-between">
        <DisableHighlightingAction
          highlightingDisabled={userSettings.highlightingDisabled}
          onChange={(disabled: boolean) =>
            setUserSettings({
              highlightingDisabled: disabled,
            })
          }
        />
        {errorLogEntries != null && errorLogEntries.length > 0 ? (
          <Tooltip label="Errors detected. Click here to see details.">
            <ActionIcon
              variant="subtle"
              aria-label="Application errors"
              size="xl"
              color="red.8"
              onClick={openErrorModal}
            >
              <IconExclamationCircle />
            </ActionIcon>
          </Tooltip>
        ) : null}
      </Group>
      <ErrorLogModal
        errorLogEntries={errorLogEntries ?? []}
        opened={errorModalOpened}
        close={closeErrorModal}
        error={error}
        isPending={isPending}
      />
    </>
  );
}
