import { ReactElement, useCallback } from 'react';
import Browser from 'webextension-polyfill';
import { type Highlight } from '@custom-types/highlight';
import { MessageType } from '@custom-types/message';
import { Tabs } from '@mantine/core';
import { useHighlights } from '@context/HighlightsContext';
import { filterHighlights } from '@utils/highlight';
import { HighlightsUrl } from '@components/sidebar/highlights/HighlightsUrl';
import { HighlightsAll } from '@components/sidebar/highlights/HighlightsAll';

type HighlightsProps = {
  searchQuery: string;
};

/**
 * Highlights for the current URL and all URLs displayed in separate tabs.
 */
export function Highlights({ searchQuery }: HighlightsProps): ReactElement {
  const { highlights, highlightsFromTab, deleteHighlight } = useHighlights();

  const onDelete = useCallback(
    async (highlight: Highlight) => {
      const tabs = await Browser.tabs.query({});
      const tabsWithHighlight = tabs.filter((tab) => tab.url === highlight.url);

      const messages: Array<Promise<void>> = [];
      tabsWithHighlight.forEach((tab) => {
        if (tab.id === undefined) {
          return;
        }
        messages.push(
          Browser.tabs.sendMessage(tab.id, {
            type: MessageType.DeleteFromHighlighter,
            payload: highlight.id,
          })
        );
      });

      for (const message of messages) {
        await message;
      }

      deleteHighlight(highlight.id);
    },
    [deleteHighlight]
  );

  return (
    <Tabs defaultValue="page-highlights">
      <Tabs.List>
        <Tabs.Tab value="page-highlights">URL</Tabs.Tab>
        <Tabs.Tab value="all-highlights">All</Tabs.Tab>
      </Tabs.List>

      <Tabs.Panel value="page-highlights">
        <HighlightsUrl
          highlights={filterHighlights(highlightsFromTab ?? [], searchQuery)}
          onDelete={onDelete}
          searchQuery={searchQuery}
        />
      </Tabs.Panel>

      <Tabs.Panel value="all-highlights">
        <HighlightsAll
          highlights={filterHighlights(highlights ?? [], searchQuery)}
          onDelete={onDelete}
          searchQuery={searchQuery}
        />
      </Tabs.Panel>
    </Tabs>
  );
}
