import { Divider, Stack } from '@mantine/core';
import { ReactElement } from 'react';
import { ExportAction } from '@components/sidebar/export/ExportAction';
import { IconCopy, IconDownload } from '@tabler/icons-react';
import { useTabInfo } from '@context/TabInfoContext';
import { useHighlightsDB } from '@context/HighlightDBContext';
import { useHighlights } from '@context/HighlightsContext';

import {
  convertHighlightsToMarkdown,
  exportString,
  getFilePath,
} from '@utils/export';
import { copyToClipboard } from '@utils/clipboard';
import { UserSettings } from '@custom-types/userSettings';
import { isMobileDevice } from '@utils/mobile';

type ExportActionsProps = {
  searchQuery: string;
  userSettings: UserSettings;
};

/**
 * Actions to export highlights such as copying or downloading.
 */
export function ExportActions({
  searchQuery,
  userSettings,
}: ExportActionsProps): ReactElement {
  const {
    tabInfo: { url, title },
  } = useTabInfo();

  const isMobile = isMobileDevice();

  const db = useHighlightsDB();
  const { markExported } = useHighlights();

  const copyHighlightsToClipboard = async () => {
    if (url === undefined) {
      return;
    }

    const markdown = await convertHighlightsToMarkdown(
      userSettings,
      searchQuery,
      db,
      markExported,
      url,
      title
    );

    return copyToClipboard(markdown);
  };

  const downloadHighlights = async () => {
    if (url === undefined) {
      return;
    }

    const markdown = await convertHighlightsToMarkdown(
      userSettings,
      searchQuery,
      db,
      markExported,
      url,
      title
    );
    exportString(
      markdown,
      getFilePath(userSettings.exportForUrl ? title : undefined)
    );
  };

  return (
    <Stack gap={0}>
      <ExportAction
        title="Copy to clipboard"
        onClick={copyHighlightsToClipboard}
        icon={<IconCopy />}
      />

      {isMobile ? null : (
        <>
          <Divider />
          <ExportAction
            title="Download to file"
            onClick={downloadHighlights}
            icon={<IconDownload />}
          />
        </>
      )}
    </Stack>
  );
}
