import { JSX } from 'react';
import { Box, Group, rem, Switch, useMantineTheme, Text } from '@mantine/core';
import { IconCheck, IconX } from '@tabler/icons-react';
type SwitchOptionProps = {
  label: string;
  description: string;
  checked: boolean;
  onChange: (checked: boolean) => void;
};

/**
 * Boolean option with a label and switch toggle.
 */
export function SwitchOption({
  label,
  description,
  checked,
  onChange,
}: SwitchOptionProps): JSX.Element {
  const theme = useMantineTheme();

  const thumbIcon = checked ? (
    <IconCheck
      style={{ width: rem(12), height: rem(12) }}
      color={theme.colors.teal[6]}
      stroke={3}
    />
  ) : (
    <IconX
      style={{ width: rem(12), height: rem(12) }}
      color={theme.colors.red[6]}
      stroke={3}
    />
  );

  return (
    <Group align="center" justify="stretch">
      <Box style={{ flex: 1 }}>
        <Text size="md">{label}</Text>
        <Text size="sm" c="dimmed">
          {description}
        </Text>
      </Box>
      <Switch
        size="md"
        checked={checked}
        onChange={(event) => onChange(event.currentTarget.checked)}
        thumbIcon={thumbIcon}
      />
    </Group>
  );
}
