import { useCallback, useEffect } from 'react';
import { Message, MessageType } from '@custom-types/message';
import { Highlight } from '@custom-types/highlight';
import { useHighlights } from '@context/HighlightsContext';
import Browser from 'webextension-polyfill';
import { getUserSettings } from '@store/userSettings';
import { UserSettings } from '@custom-types/userSettings';
import { useHighlightsDB } from '@context/HighlightDBContext';

/**
 * Handle messages from the content script.
 */
export function useMessageHandler(): void {
  const { refetchHighlights, refetchHighlightsFromTab } = useHighlights();
  const db = useHighlightsDB();

  const messageHandler = useCallback(
    async (
      message: Message
    ): Promise<void | Highlight[] | Highlight | UserSettings> => {
      switch (message.type) {
        case MessageType.RefetchHighlights:
          refetchHighlights();
          refetchHighlightsFromTab();
          break;
        // We need to handle GetHighlight(s) and GetUserSettings here as well to avoid getting
        // undefined responses in the content script.
        case MessageType.GetHighlights:
          return db.getHighlightsForUrl(message.payload as string);
        case MessageType.GetHighlight:
          return db.getHighlight(message.payload as string);
        case MessageType.GetUserSettings:
          return getUserSettings();
      }
    },
    [db, refetchHighlights, refetchHighlightsFromTab]
  );

  useEffect(() => {
    const typedHandler = (message: unknown) =>
      messageHandler(message as Message);
    Browser.runtime.onMessage.addListener(typedHandler);
    return () => {
      Browser.runtime.onMessage.removeListener(typedHandler);
    };
  }, [messageHandler]);
}
