import { ReactElement } from 'react';
import { Highlight as HighlightType } from '@custom-types/highlight';
import { Text, Card, Stack } from '@mantine/core';
import { useUserSettings } from '@context/UserSettingsContext';
import Markdown from 'react-markdown';
import { SidebarCommentEditor } from '@components/sidebar/highlights/highlight/SidebarCommentEditor';
import { ActionMenu } from '@components/sidebar/highlights/highlight/ActionMenu';
import { ExportIndicator } from '@components/sidebar/highlights/highlight/ExportIndicator';

import './Highlight.css';

type HighlightProps = {
  highlight: HighlightType;
  onDelete: (highlight: HighlightType) => void;
  allHighlights: boolean;
};

/**
 * Display a single highlight.
 */
export function Highlight({
  highlight,
  onDelete,
  allHighlights,
}: HighlightProps): ReactElement {
  const { userSettings } = useUserSettings();
  const applyHiddenHighlightStyles = highlight.hidden && !allHighlights;

  return (
    <Card
      shadow="sm"
      padding="sm"
      radius="md"
      withBorder
      w="100%"
      className="highlight-container"
      style={{
        borderLeftColor: applyHiddenHighlightStyles
          ? undefined
          : highlight.color,
        filter: applyHiddenHighlightStyles ? 'brightness(80%)' : undefined,
      }}
    >
      <div className="highlight-flex-box">
        <div className="highlight-stretch">
          <Stack gap="xs">
            {userSettings.displayHighlightsAsText ? (
              <Text className="highlight-text">{highlight.text}</Text>
            ) : (
              <div className="highlight-html">
                <Markdown>{highlight.markdown}</Markdown>
              </div>
            )}
            <SidebarCommentEditor
              highlight={highlight}
              key={highlight.comment}
            />
          </Stack>
        </div>

        <Stack justify="space-between" align="center">
          <ActionMenu
            highlight={highlight}
            onDelete={onDelete}
            allHighlights={allHighlights}
          />
          <ExportIndicator exported={highlight.exported} />
        </Stack>
      </div>
    </Card>
  );
}
