import { Highlight } from '@custom-types/highlight';
import { createContext, JSX, ReactNode, useContext } from 'react';
import { useHighlightsDB } from '@context/HighlightDBContext';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useTabInfo } from '@context/TabInfoContext';

type HighlightContextType = {
  highlights: Highlight[] | undefined;
  highlightsFromTab: Highlight[] | undefined;
  markExported: (highlights: Highlight[]) => void;
  deleteHighlight: (highlightId: string) => void;
  updateHighlight: (highlight: Highlight) => void;
  refetchHighlights: () => void;
  refetchHighlightsFromTab: () => void;
};

/**
 * Context to store and retrieve highlights.
 */
const HighlightsContext = createContext<HighlightContextType | null>(null);

type HighlightProviderProps = {
  children: ReactNode;
};

/**
 * Provider for the HighlightsContext.
 */
export function HighlightsProvider({
  children,
}: HighlightProviderProps): JSX.Element {
  const db = useHighlightsDB();
  const { tabInfo } = useTabInfo();
  const queryClient = useQueryClient();

  const highlightsFromTabQueryKey = ['highlights-for-url', tabInfo.url, db];
  const highlightsQueryKey = ['highlights', db];

  const { data: highlightsFromTab, refetch: refetchHighlightsFromTab } =
    useQuery({
      queryKey: highlightsFromTabQueryKey,
      queryFn: () => db.getHighlightsForUrl(tabInfo.url!),
      enabled: tabInfo.url != null,
    });

  const { data: highlights, refetch: refetchHighlights } = useQuery({
    queryKey: highlightsQueryKey,
    queryFn: () => db.getHighlights(),
  });

  const markExported = useMutation({
    mutationFn: (highlights: Highlight[]) =>
      db.markHighlightsAsExported(highlights),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: highlightsFromTabQueryKey });
      queryClient.invalidateQueries({ queryKey: highlightsQueryKey });
    },
  }).mutate;

  const deleteHighlight = useMutation({
    mutationFn: (highlightId: string) => db.removeHighlight(highlightId),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: highlightsFromTabQueryKey });
      queryClient.invalidateQueries({ queryKey: highlightsQueryKey });
    },
  }).mutate;

  const updateHighlight = useMutation({
    mutationFn: (highlight: Highlight) => db.updateHighlight(highlight),
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: highlightsFromTabQueryKey });
      queryClient.invalidateQueries({ queryKey: highlightsQueryKey });
    },
  }).mutate;

  return (
    <HighlightsContext.Provider
      value={{
        highlights,
        highlightsFromTab,
        markExported,
        updateHighlight,
        deleteHighlight,
        refetchHighlights,
        refetchHighlightsFromTab,
      }}
    >
      {children}
    </HighlightsContext.Provider>
  );
}

/**
 * Hook to access and set highlights.
 */
export function useHighlights(): HighlightContextType {
  const context = useContext(HighlightsContext);
  if (context == null) {
    throw new Error('useHighlights must be used within a HighlightProvider');
  }
  return context;
}
