import { ReactElement } from 'react';
import { SettingsSection } from '@components/sidebar/settings/SettingsSection';
import { SwitchOption } from '@components/sidebar/settings/options/SwitchOption';
import { UserSettings } from '@custom-types/userSettings';

type ExportSettingsProps = {
  userSettings: UserSettings;
  setUserSettings: (userSettings: Partial<UserSettings>) => void;
  searchQuery: string;
  withDivider?: boolean;
};

/**
 * Settings for the export of highlights.
 */
export function ExportSettings({
  userSettings,
  setUserSettings,
  searchQuery,
  withDivider,
}: ExportSettingsProps): ReactElement {
  const searchEnabled = searchQuery != null && searchQuery.trim() !== '';
  let filteredHighlightsDescription =
    'Export only highlights that match the current search query';
  if (searchEnabled) {
    filteredHighlightsDescription += ` '${searchQuery}'.`;
  } else {
    filteredHighlightsDescription +=
      '. The search query is currently empty so that all highlights will be exported.';
  }

  return (
    <SettingsSection title="Export Settings" withDivider={withDivider}>
      <SwitchOption
        label="Highlights for current page only"
        description="Toggle to restrict exports to the current page only."
        checked={userSettings.exportForUrl}
        onChange={(checked) => setUserSettings({ exportForUrl: checked })}
      />
      <SwitchOption
        label="New highlights only"
        description="Export only new highlights (indicated by a star)."
        checked={userSettings.exportNewHighlightsOnly}
        onChange={(checked) =>
          setUserSettings({ exportNewHighlightsOnly: checked })
        }
      />
      <SwitchOption
        label="Filtered highlights only"
        description={filteredHighlightsDescription}
        checked={userSettings.exportFilteredOnly}
        onChange={(checked) => setUserSettings({ exportFilteredOnly: checked })}
      />
      <SwitchOption
        label="Remove highlight formatting"
        description="Export highlights as plain text without the formatting."
        checked={userSettings.exportHighlightsFromText}
        onChange={(checked) =>
          setUserSettings({ exportHighlightsFromText: checked })
        }
      />
    </SettingsSection>
  );
}
