import {
  createContext,
  JSX,
  ReactNode,
  useContext,
  useEffect,
  useState,
} from 'react';
import {
  DEFAULT_USER_SETTINGS,
  UserSettings,
} from '@custom-types/userSettings';
import { getUserSettings, storeUserSettings } from '@store/userSettings';
import { MessageType } from '@custom-types/message';
import { sendToTabs } from '@utils/message';

type UserSettingsContextType = {
  userSettings: UserSettings;
  setUserSettings: (userSettings: Partial<UserSettings>) => void;
};

/**
 * Context to store and retrieve user settings.
 */
const UserSettingsContext = createContext<UserSettingsContextType | null>(null);

type UserSettingsProviderProps = {
  children: ReactNode;
};

/**
 * Provider for the UserSettingsContext.
 */
export function UserSettingsProvider({
  children,
}: UserSettingsProviderProps): JSX.Element {
  const [userSettings, setUserSettings] = useState<UserSettings>(
    DEFAULT_USER_SETTINGS
  );

  const syncUserSettings = async (
    userSettings: Partial<UserSettings>
  ): Promise<void> => {
    setUserSettings((prevUserSettings) => {
      const updatedUserSettings = { ...prevUserSettings, ...userSettings };
      storeUserSettings(updatedUserSettings);
      sendToTabs({
        type: MessageType.UserSettingsUpdate,
        payload: updatedUserSettings,
      });
      return updatedUserSettings;
    });
  };

  useEffect(() => {
    async function initUserSettings(): Promise<void> {
      let userSettings = await getUserSettings();
      if (userSettings === undefined) {
        userSettings = DEFAULT_USER_SETTINGS;
      }
      syncUserSettings(userSettings);
    }
    initUserSettings();
  }, []);

  return (
    <UserSettingsContext.Provider
      value={{
        userSettings,
        setUserSettings: syncUserSettings,
      }}
    >
      {children}
    </UserSettingsContext.Provider>
  );
}

/**
 * Hook to access and set user settings.
 */
export function useUserSettings(): UserSettingsContextType {
  const context = useContext(UserSettingsContext);
  if (context == null) {
    throw new Error(
      'useUserSettings must be used within a UserSettingsProvider'
    );
  }
  return context;
}
