import { ReactElement } from 'react';
import { ActionIcon, Tooltip } from '@mantine/core';
import { IconCircleOff, IconPower } from '@tabler/icons-react';

type DisableHighlightingActionProps = {
  highlightingDisabled: boolean;
  onChange: (disabled: boolean) => void;
};

/**
 * An action to disable the highlighting.
 * Sets the setting to disable highlighting when clicked.
 * The content script reacts to the change and removes highlights and disables highlighting.
 */
export function DisableHighlightingAction({
  highlightingDisabled,
  onChange,
}: DisableHighlightingActionProps): ReactElement {
  return (
    <Tooltip
      label={
        highlightingDisabled ? 'Enable highlighting' : 'Disable highlighting'
      }
    >
      <ActionIcon
        variant="subtle"
        size="xl"
        color={highlightingDisabled ? 'red' : undefined}
        onClick={() => onChange(!highlightingDisabled)}
        aria-label={
          highlightingDisabled ? 'Enable highlighting' : 'Disable highlighting'
        }
      >
        {highlightingDisabled ? <IconCircleOff /> : <IconPower />}
      </ActionIcon>
    </Tooltip>
  );
}
