import React from 'react';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { MantineProvider, MantineThemeOverride } from '@mantine/core';
import { TabInfoProvider } from '@context/TabInfoContext';
import { HighlightsDBProvider } from '@context/HighlightDBContext';
import { UserSettingsProvider } from '@context/UserSettingsContext';
import { HighlightsProvider } from '@context/HighlightsContext';
import { Sidebar } from '@components/sidebar/Sidebar';
import { HighlightDatabase, HIGHLIGHT_DB_VERSION } from '@store/highlights';

export const theme: MantineThemeOverride = {
  primaryColor: 'cyan',
};

const queryClient = new QueryClient();
const highlightsDB = new HighlightDatabase(HIGHLIGHT_DB_VERSION);

/**
 * Entrypoint of the React application. Used as a sidebar for Desktop browsers and as a popup for Firefox for Android.
 */
export function App() {
  return (
    <React.StrictMode>
      <QueryClientProvider client={queryClient}>
        <MantineProvider defaultColorScheme="dark" theme={theme}>
          <TabInfoProvider>
            <HighlightsDBProvider highlightDatabase={highlightsDB}>
              <UserSettingsProvider>
                <HighlightsProvider>
                  <Sidebar />
                </HighlightsProvider>
              </UserSettingsProvider>
            </HighlightsDBProvider>
          </TabInfoProvider>
        </MantineProvider>
      </QueryClientProvider>
    </React.StrictMode>
  );
}
