import { ErrorLogEntry } from '@custom-types/errorLog';
import Browser from 'webextension-polyfill';
import {
  useMutation,
  UseMutationResult,
  useQuery,
  useQueryClient,
  UseQueryResult,
} from '@tanstack/react-query';

const QUERY_KEY = 'errorLogEntries';

/** Query hook to retrieve error logs. */
export function useErrorLog(
  refetchInterval?: number
): UseQueryResult<ErrorLogEntry[], Error> {
  return useQuery({
    queryKey: [QUERY_KEY],
    queryFn: () => getErrorLogEntries(),
    refetchInterval,
  });
}

/** Mutation hook to delete an error log entry. */
export function useDeleteErrorLogEntry(): UseMutationResult<
  void,
  Error,
  string
> {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: (id: string) => deleteErrorLogEntry(id),
    onSuccess: () => queryClient.invalidateQueries({ queryKey: [QUERY_KEY] }),
  });
}

/**
 * Store a log entry in local storage.
 * If there already exists a log entry with the same ID, it will be overwritten.
 */
export async function storeErrorLogEntry(
  errorLogEntry: ErrorLogEntry
): Promise<void> {
  const errorLogEntries = await getErrorLogEntries();
  if (errorLogEntries.some((entry) => entry.id === errorLogEntry.id)) {
    await updateErrorLogEntry(errorLogEntries, errorLogEntry);
    return;
  }

  const updatedErrorLogEntries = [errorLogEntry, ...errorLogEntries];
  await Browser.storage.local.set({
    errorLogEntries: updatedErrorLogEntries,
  });
}

/** Update a log entry in local storage. */
async function updateErrorLogEntry(
  errorLogEntries: ErrorLogEntry[],
  updatedLogEntry: ErrorLogEntry
): Promise<void> {
  const updatedErrorLogEntries = errorLogEntries.map((entry) =>
    entry.id === updatedLogEntry.id ? updatedLogEntry : entry
  );

  await Browser.storage.local.set({
    errorLogEntries: updatedErrorLogEntries,
  });
}

/** Delete a log entry from local storage. */
export async function deleteErrorLogEntry(id: string): Promise<void> {
  const errorLogEntries = await getErrorLogEntries();
  const updatedErrorLogEntries = errorLogEntries.filter(
    (errorLogEntry) => errorLogEntry.id !== id
  );

  await Browser.storage.local.set({
    errorLogEntries: updatedErrorLogEntries,
  });
}

/** Retrieve the error logs from local storage. */
export async function getErrorLogEntries(): Promise<ErrorLogEntry[]> {
  const errorLogEntryStore: { errorLogEntries?: ErrorLogEntry[] } =
    await Browser.storage.local.get('errorLogEntries');
  if (errorLogEntryStore.errorLogEntries === undefined) {
    return [];
  }

  return errorLogEntryStore.errorLogEntries;
}
