import {
  openDB,
  deleteDB,
  DBSchema,
  IDBPDatabase,
  IDBPTransaction,
  StoreNames,
} from 'idb';
import { Highlight } from '@custom-types/highlight';
import { logAndPushError } from '@utils/log';

/**
 * The current version of the highlight database.
 */
export const HIGHLIGHT_DB_VERSION = 4;

interface HighlightDB extends DBSchema {
  highlights: {
    key: string;
    value: Highlight;
    indexes: { 'url-index': string };
  };
}

/**
 * IndexedDB database to store and retrieve highlights.
 */
export class HighlightDatabase {
  private static readonly STORE_NAME = 'highlights';

  private static readonly DATABASE_NAME = HighlightDatabase.STORE_NAME + '-db';

  private db?: IDBPDatabase<HighlightDB>;

  public readonly databaseInitialized: Promise<void>;

  constructor(version: number) {
    this.databaseInitialized = this.initDatabase(version);
  }

  private async initDatabase(version: number): Promise<void> {
    try {
      this.db = await openDB<HighlightDB>(
        HighlightDatabase.DATABASE_NAME,
        version,
        {
          upgrade: (db, oldVersion, _, transaction) =>
            this.migrateDatabase(db, oldVersion, transaction),
        }
      );
    } catch (error) {
      HighlightDatabase.assertIsError(error);
      logAndPushError(
        'Error initializing database',
        `If highlights are not stored, please try to reload the page or restart the browser to reload the extension. ${error ? 'Error: ' + error.message : ''}`
      );
      console.error('Error initializing database', error);
    }
  }

  /**
   * Applies all the necessary migrations from the first to the latest version.
   */
  public async migrateDatabase(
    db: IDBPDatabase<HighlightDB>,
    oldVersion: number,
    transaction: IDBPTransaction<
      HighlightDB,
      StoreNames<HighlightDB>[],
      'versionchange'
    >
  ): Promise<void> {
    switch (oldVersion) {
      // v0 -> v1
      case 0: {
        const objectStore = db.createObjectStore(HighlightDatabase.STORE_NAME, {
          keyPath: 'id',
        });
        objectStore.createIndex('url-index', 'url', {
          unique: false,
        });
      }
      // v1 -> v2
      // intentional fallthrough
      case 1: {
        // Add a markdown property to each highlight.
        const store = transaction.objectStore(HighlightDatabase.STORE_NAME);
        let cursor = await store.openCursor();
        while (cursor) {
          const highlight: Highlight = cursor.value;
          if (highlight.markdown === undefined) {
            highlight.markdown = highlight.text;
            await cursor.update(highlight);
          }
          cursor = await cursor.continue();
        }
      }
      // v2 -> v3
      // intentional fallthrough
      case 2: {
        // Convert Date objects for the created date to ISO strings.
        const store = transaction.objectStore(HighlightDatabase.STORE_NAME);
        let cursor = await store.openCursor();
        while (cursor) {
          const highlight: Highlight = cursor.value;
          // @ts-expect-error createdAt is a Date object in version 2
          if (highlight.createdAt instanceof Date) {
            highlight.createdAt = (
              highlight.createdAt as unknown as Date
            ).toISOString();
            await cursor.update(highlight);
          }
          cursor = await cursor.continue();
        }
      }
      // v3 -> v4
      // intentional fallthrough
      case 3: {
        // Set the newly introduced updatedAt date of highlights to the creation date.
        // Set the hidden field to false.
        const store = transaction.objectStore(HighlightDatabase.STORE_NAME);
        let cursor = await store.openCursor();
        while (cursor) {
          const highlight: Highlight = cursor.value;
          if (highlight.updatedAt === undefined) {
            highlight.updatedAt = highlight.createdAt;
          }

          if (highlight.hidden === undefined) {
            highlight.hidden = false;
          }

          await cursor.update(highlight);
          cursor = await cursor.continue();
        }
      }
    }
  }

  /** Store a highlight in the database. */
  public async storeHighlight(highlight: Highlight) {
    HighlightDatabase.assertDatabaseInitialized(this.db);

    await this.db.add(HighlightDatabase.STORE_NAME, highlight);
  }

  /** Remove a highlight from the database. */
  public async removeHighlight(id: string) {
    HighlightDatabase.assertDatabaseInitialized(this.db);

    await this.db.delete(HighlightDatabase.STORE_NAME, id);
  }

  /** Update a highlight in the database. Updates the updatedAt date.  */
  public async updateHighlight(highlight: Highlight) {
    HighlightDatabase.assertDatabaseInitialized(this.db);

    highlight.updatedAt = new Date().toISOString();
    await this.db.put(HighlightDatabase.STORE_NAME, highlight);
  }

  /** Get all highlights from the database. */
  public async getHighlights(): Promise<Highlight[]> {
    HighlightDatabase.assertDatabaseInitialized(this.db);

    return await this.db.getAll(HighlightDatabase.STORE_NAME);
  }

  /** Get a single highlight by its ID. */
  public async getHighlight(id: string): Promise<Highlight> {
    HighlightDatabase.assertDatabaseInitialized(this.db);

    const highlight = await this.db.get(HighlightDatabase.STORE_NAME, id);
    if (highlight === undefined) {
      throw new Error(`No highlight with ID '${id}' found.`);
    }

    return highlight;
  }

  /** Get highlights for a given URL from the database. */
  public async getHighlightsForUrl(url: string): Promise<Highlight[]> {
    HighlightDatabase.assertDatabaseInitialized(this.db);

    return await this.db.getAllFromIndex(
      HighlightDatabase.STORE_NAME,
      'url-index',
      url
    );
  }

  /** Get all highlights from the database that have not previously been exported. */
  public async getNonExportedHighlights(): Promise<Highlight[]> {
    const highlights = await this.getHighlights();
    return highlights.filter((highlight) => !highlight.exported);
  }

  /** Get highlights for a given URL from the database that have not previously been exported. */
  public async getNonExportedHighlightsForUrl(
    url: string
  ): Promise<Highlight[]> {
    const highlights = await this.getHighlightsForUrl(url);
    return highlights.filter((highlight) => !highlight.exported);
  }

  /**
   * Marks the given highlights as exported.
   */
  public async markHighlightsAsExported(
    highlights: Highlight[]
  ): Promise<void> {
    return this.setHighlightExportStatus(highlights, true);
  }

  /**
   * Set the export status of the given highlights.
   */
  public async setHighlightExportStatus(
    highlights: Highlight[],
    exported: boolean
  ): Promise<void> {
    HighlightDatabase.assertDatabaseInitialized(this.db);

    const tx = this.db.transaction(HighlightDatabase.STORE_NAME, 'readwrite');
    const store = tx.objectStore(HighlightDatabase.STORE_NAME);

    const updatePromises = highlights.map((highlight) => {
      highlight.exported = exported;
      return store.put(highlight);
    });

    await Promise.all(updatePromises);
    await tx.done;
  }

  /**
   * Close the connection to the database.
   */
  public close(): void {
    this.db?.close();
  }

  /**
   * Only use this method for testing. Irreversibly deletes the whole database.
   */
  public unsafeDelete(): Promise<void> {
    this.close();
    return deleteDB(HighlightDatabase.DATABASE_NAME);
  }

  private static assertIsError(error: unknown): asserts error is Error {
    if (!(error instanceof Error)) {
      throw error;
    }
  }

  private static assertDatabaseInitialized(
    db: IDBPDatabase<HighlightDB> | undefined
  ): asserts db is IDBPDatabase<HighlightDB> {
    if (db == null) {
      throw new Error('Database not initialized');
    }
  }
}
