import { ReactNode } from 'react';
import { Alert, Group, Stack } from '@mantine/core';
import { ErrorLogEntry } from '@custom-types/errorLog';

export type LogEntryProps = {
  errorLogEntry: ErrorLogEntry;
  deleteEntry: () => void;
};

/**
 * A single entry in the log.
 */
export function LogEntry({
  errorLogEntry,
  deleteEntry,
}: LogEntryProps): ReactNode {
  return (
    <Alert
      variant="light"
      color="red"
      title={errorLogEntry.title}
      withCloseButton
      onClose={deleteEntry}
    >
      <Stack gap="xs">
        {errorLogEntry.description}
        <Group justify="flex-end" c="dimmed">
          {errorLogEntry.createdAt.toLocaleString()}
        </Group>
      </Stack>
    </Alert>
  );
}
