import { Highlight } from '@custom-types/highlight';

/**
 * Converts the text of a highlight to a markdown string.
 */
export function highlightTextToMarkdown(
  highlight: Highlight,
  includeUrl = true
): string {
  return stringToMarkdown(
    highlight.text,
    highlight.comment,
    includeUrl ? highlight.url : undefined
  );
}

/**
 * Optionally extends the markdown of a highlight with additional metadata such
 * as the URL as source and the comment added to a highlight.
 */
export function extendMarkdownOfHighlightWithMetaData(
  highlight: Highlight,
  includeUrl = true
) {
  return stringToMarkdown(
    highlight.markdown,
    highlight.comment,
    includeUrl ? highlight.url : undefined
  );
}

function stringToMarkdown(
  text: string,
  comment?: string,
  url?: string
): string {
  if (comment !== undefined && comment !== '') {
    text += `\n*${comment}*`;
  }

  if (url !== undefined) {
    text += `\n-- [source](${url})`;
  }

  return text;
}

/**
 * Converts the text of multiple highlights to a markdown string.
 */
export function highlightsTextToMarkdown(
  highlights: Highlight[],
  url: string,
  title?: string
): string {
  return highlightsToMarkdown(highlightTextToMarkdown, highlights, url, title);
}

/**
 * Converts the markdown of multiple highlights to one markdown string including the URL as source and an optional title.
 */
export function extendMarkdownOfHighlightsWithSource(
  highlights: Highlight[],
  url: string,
  title?: string
): string {
  return highlightsToMarkdown(
    extendMarkdownOfHighlightWithMetaData,
    highlights,
    url,
    title,
    false
  );
}

/**
 * Converts the markdown of highlights grouped by URL to one markdown string.
 */
export function groupedHighlightsMarkdownToMarkdown(
  highlightsByUrl: Map<string, Highlight[]>
): string {
  return groupedHighlightsToMarkdown(
    extendMarkdownOfHighlightsWithSource,
    highlightsByUrl
  );
}

/**
 * Converts the text of highlights grouped by URL to a markdown string.
 */
export function groupedHighlightsTextToMarkdown(
  highlightsByUrl: Map<string, Highlight[]>
): string {
  return groupedHighlightsToMarkdown(highlightsTextToMarkdown, highlightsByUrl);
}

/**
 * Converts highlights grouped by URL to a markdown string.
 */
function groupedHighlightsToMarkdown(
  highlightsConverter: (
    highlights: Highlight[],
    url: string,
    title?: string
  ) => string,
  highlightsByUrl: Map<string, Highlight[]>
): string {
  return Array.from(highlightsByUrl).reduce((markdown, [url, highlights]) => {
    return `${markdown}${highlightsConverter(highlights, url)}\n`;
  }, '');
}

/**
 * Converts highlights to a string applying the highlightConverter function to each highlight.
 */
function highlightsToMarkdown(
  highlightConverter: (highlight: Highlight, includeUrl: boolean) => string,
  highlights: Highlight[],
  url: string,
  title?: string,
  additionalNewLine = true
): string {
  const titleMarkdown = getTitleMarkdown(url, title);
  const highlightsMarkdown = highlights
    .map((highlight) => highlightConverter(highlight, false))
    .join(additionalNewLine ? '\n\n' : '\n');

  return titleMarkdown + highlightsMarkdown;
}

function getTitleMarkdown(url: string, title?: string): string {
  return title ? `## [${title}](${url})\n` : `## ${url}\n`;
}
