import {
  createContext,
  Dispatch,
  JSX,
  ReactNode,
  SetStateAction,
  useContext,
  useState,
} from 'react';
import { TabInfo } from '@custom-types/tabInfo';

type TabInfoContextType = {
  tabInfo: TabInfo;
  setTabInfo: Dispatch<SetStateAction<TabInfo>>;
};

/**
 * Context to store and retrieve tab information.
 */
const TabInfoContext = createContext<TabInfoContextType | null>(null);

type TabInfoProviderProps = {
  children: ReactNode;
};

/**
 * Provider for the TabInfoContext.
 */
export function TabInfoProvider({
  children,
}: TabInfoProviderProps): JSX.Element {
  const [tabInfo, setTabInfo] = useState<TabInfo>({});

  return (
    <TabInfoContext.Provider
      value={{
        tabInfo,
        setTabInfo,
      }}
    >
      {children}
    </TabInfoContext.Provider>
  );
}

/**
 * Hook to access and set tab infos.
 */
export function useTabInfo(): TabInfoContextType {
  const context = useContext(TabInfoContext);
  if (context == null) {
    throw new Error('useTabInfo must be used within a TabInfoProvider');
  }
  return context;
}
