import { JSX, useMemo } from 'react';
import { Stack } from '@mantine/core';
import { SwitchOption } from '@components/sidebar/settings/options/SwitchOption';
import { useUserSettings } from '@context/UserSettingsContext';
import { SettingsSection } from '@components/sidebar/settings/SettingsSection';
import { isMobileDevice } from '@utils/mobile';
import { ExportSettings } from '@components/sidebar/settings/export/ExportSettings';

type SettingsProps = {
  searchQuery: string;
};

/**
 * Page for settings.
 */
export function Settings({ searchQuery }: SettingsProps): JSX.Element {
  const { userSettings, setUserSettings } = useUserSettings();
  const isMobile = useMemo(() => isMobileDevice(), []);

  return (
    <Stack p={8}>
      <SettingsSection title="Higlighting" withDivider>
        <>
          {isMobile ? null : (
            <SwitchOption
              label="Auto highlighting"
              description="Automatically create a highlight when selecting text."
              checked={userSettings.autoHighlighting}
              onChange={(checked) =>
                setUserSettings({ autoHighlighting: checked })
              }
            />
          )}
        </>
        <SwitchOption
          label="Extended formatting"
          description="Enable additional formatting options such as headings, lists, or code when creating highlights."
          checked={userSettings.extendedHighlighting}
          onChange={(checked) =>
            setUserSettings({ extendedHighlighting: checked })
          }
        />
        <SwitchOption
          label="Display highlights as text"
          description="Display highlights as text without any formatting instead of markdown in the sidebar."
          checked={userSettings.displayHighlightsAsText}
          onChange={(checked) =>
            setUserSettings({ displayHighlightsAsText: checked })
          }
        />
      </SettingsSection>
      <ExportSettings
        userSettings={userSettings}
        setUserSettings={setUserSettings}
        searchQuery={searchQuery}
      />
    </Stack>
  );
}
