import { ActionIcon, Menu } from '@mantine/core';
import { IconDotsVertical } from '@tabler/icons-react';
import { extendMarkdownOfHighlightWithMetaData } from '@utils/markdown';
import { copyToClipboard } from '@utils/clipboard';
import { ReactElement } from 'react';
import { useHighlights } from '@context/HighlightsContext';
import { type Highlight } from '@custom-types/highlight';
import { getActiveTab } from '@utils/tab';
import Browser from 'webextension-polyfill';
import { MessageType } from '@custom-types/message';
import { useHighlightsDB } from '@context/HighlightDBContext';

type ActionMenuProps = {
  highlight: Highlight;
  onDelete: (highlight: Highlight) => void;
  allHighlights: boolean;
};

/** Actions for a single highlight in the sidebar. */
export function ActionMenu({
  highlight,
  onDelete,
  allHighlights,
}: ActionMenuProps): ReactElement {
  const { markExported, updateHighlight } = useHighlights();

  const copyAndMarkExported = (text: string) => {
    copyToClipboard(text);
    markExported([highlight]);
  };

  const db = useHighlightsDB();

  const toggleHiddenState = async () => {
    const updatedHighlight: Highlight = {
      ...highlight,
      hidden: !highlight.hidden,
    };
    updateHighlight(updatedHighlight);

    // Update the highlights of the currently active tab.
    const activeTab = await getActiveTab();
    if (
      activeTab !== null &&
      activeTab.id !== undefined &&
      activeTab.url !== undefined
    ) {
      const highlights = await db.getHighlightsForUrl(activeTab.url);
      await Browser.tabs.sendMessage(activeTab.id, {
        type: MessageType.TabUpdate,
        payload: highlights,
      });
    }
  };

  const deleteComment = () => {
    const updatedHighlight: Highlight = {
      ...highlight,
      comment: undefined,
    };
    updateHighlight(updatedHighlight);
  };

  const addComment = () => {
    const updatedHighlight: Highlight = {
      ...highlight,
      comment: '',
    };
    updateHighlight(updatedHighlight);
  };

  return (
    <Menu shadow="md">
      <Menu.Target>
        <ActionIcon variant="subtle" color="white">
          <IconDotsVertical />
        </ActionIcon>
      </Menu.Target>

      <Menu.Dropdown>
        <Menu.Label>Edit</Menu.Label>
        {!allHighlights ? (
          <Menu.Item onClick={toggleHiddenState}>
            {highlight.hidden ? 'Show highlight' : 'Hide highlight'}
          </Menu.Item>
        ) : null}
        {highlight.comment == null ? (
          <Menu.Item onClick={addComment}>Add comment</Menu.Item>
        ) : (
          <Menu.Item onClick={deleteComment}>Delete comment</Menu.Item>
        )}
        <Menu.Divider />
        <Menu.Label>Clipboard</Menu.Label>
        <Menu.Item onClick={() => copyAndMarkExported(highlight.text)}>
          Copy text
        </Menu.Item>
        {highlight.comment !== undefined && highlight.comment !== '' ? (
          <Menu.Item onClick={() => copyAndMarkExported(highlight.comment!)}>
            Copy comment
          </Menu.Item>
        ) : null}
        <Menu.Item
          onClick={() =>
            copyAndMarkExported(
              extendMarkdownOfHighlightWithMetaData(highlight)
            )
          }
        >
          Copy markdown
        </Menu.Item>
        <Menu.Item onClick={() => copyToClipboard(highlight.url)}>
          Copy URL
        </Menu.Item>
        <Menu.Label>Delete</Menu.Label>
        <Menu.Item color="red" onClick={() => onDelete(highlight)}>
          Delete Highlight
        </Menu.Item>
      </Menu.Dropdown>
    </Menu>
  );
}
