import { type JSX } from 'react';
import { Card, Text } from '@mantine/core';

type NoHighlightsProps = {
  searchQuery: string;
};

/**
 * An info message displayed when there are no highlights.
 */
export function NoHighlights({ searchQuery }: NoHighlightsProps): JSX.Element {
  return (
    <Card m={16} p={16}>
      <Text mb={8}>No highlights here.</Text>
      {searchQuery ? (
        <Text mb={8}>
          The current filter query is <strong>{searchQuery}</strong>.
        </Text>
      ) : null}
      <Text>Mark text on a website to create a highlight</Text>
    </Card>
  );
}
